<template>
  <div class="container">
    <div class="row">
      <div class="title">
        <h1 class="h1">Cards</h1>
        <div class="dots htooltip">
          <div class="htooltip-inner">
            <button class="task-control" @click="reverseTasks">
              Reverse Tasks
            </button>
          </div>
        </div>
      </div>

      <Task v-on:toFirstPage="toFirstPage" />
      <TasksList />
      <Pagination
        :total-items="countTasks"
        :current-page="currentPage"
        @pagechanged="onPageChange"
      />
    </div>
  </div>
</template>
<script>
import Task from "./Task.vue";
import TasksList from "./TasksList.vue";
import Pagination from "./Pagination.vue";
import { mapGetters, mapState } from "vuex";
export default {
  name: "App",
  beforeCreate() {
    if (
      (this.$store.getters["getTasks"].length < this.$route.params.id * 10 &&
        this.$route.params.id != 1) ||
      !isInt(this.$route.params.id)
    ) {
      this.$router.push({ path: `/error` });
    }
    this.$store.commit("setPage", +this.$route.params.id);
  },
  created() {
    this.tasks = this.$store.dispatch("loadItems");
  },
  components: {
    Task,
    TasksList,
    Pagination
  },
  methods: {
    onPageChange(page) {
      this.$store.commit("setPage", page);

      this.$router.push({ path: `/page/${page}` });
    },
    toFirstPage() {
      this.$store.commit("setPage", 1);
    },
    reverseTasks() {
      this.$store.commit("reverseTasks");
    }
  },

  computed: {
    ...mapGetters({
      countTasks: "countTasks",
      curentTasks: "curentTasks",
      currentPage: "currentPage",
      countTasks: "countTasks"
    })
  }
};
function isInt(value) {
  if (isNaN(value)) {
    return false;
  }
  var x = parseFloat(value);
  return (x | 0) === x;
}
</script>

<style>
*,
*::after,
*::before {
  box-sizing: border-box;
  font-family: Arial;
}
a {
  text-decoration: none;
}
body {
  background-color: #222f3e;
  color: #243849;

  font-size: 14px;
}
.list-reset {
  margin: 0;
  padding: 0;

  list-style-type: none;
}
.container {
  max-width: 380px;

  margin: 0 auto;
}
.h1 {
  margin: 0 0 5px;
}
.row {
  padding: 10px;
  background-color: #e0e3e6;
  border-radius: 5px;
}
.title {
  display: flex;
  justify-content: space-between;
}
</style>



