<template>
  <div>
    <div v-if="isLoading" class="lds-ellipsis">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
    <transition-group name="list" tag="ul" class="list-reset">
      <li
        class="box"
        :class="{ done: task.completed }"
        v-for="task in tasks"
        :key="task.id"
        @dblclick="completeTask({ id: task.id, completed: task.completed })"
      >
        <p class="task-description">
          {{ task.title }}
        </p>
        <div class="dots htooltip">
          <div class="htooltip-inner">
            <button class="task-control" @click="deleteTask(task.id)">
              &#10006; Delete
            </button>
            <router-link
              class="task-control"
              tag="button"
              :to="'/edit/' + task.id"
            >
              <a class="link">&#9998;Edit</a>
            </router-link>
          </div>
        </div>
      </li>
    </transition-group>
  </div>
</template>

<script>
export default {
  computed: {
    tasks() {
      return this.$store.getters["currentTasks"];
    },
    isLoading() {
      return this.$store.getters["isLoading"];
    }
  },
  methods: {
    deleteTask(id) {
      this.$store.dispatch("deleteTask", id);
      if (this.tasks.length == 1) {
        this.$store.commit("setPage", this.$store.getters["currentPage"] - 1);
      }
    },
    completeTask(id) {
      this.$store.dispatch("completeTask", id);
    }
  }
};
</script>
<style>
.box {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  border-radius: 10px;
  margin: 10px 0;
  padding: 0 0 0 10px;
  border: 1px solid #fff;
}
.task-description {
  margin: 10px;
}
.task-control {
  align-self: flex-start;
}
.done {
  text-decoration: line-through;
  opacity: 0.5;
  border: 1px solid #7ca65c;
}
.dots {
  display: block;
  margin: 10px 10px 10px 0;
  padding: 10px;
  width: 15px;
  height: 15px;
  background: url(".././assets/dots.svg") no-repeat;
  color: #222f3e;
  background-size: 15px 15px;
}
.link {
  color: inherit;
}
.list-enter-active {
  transform: scale(1);
  transition: all 0.5s;
}
.list-enter {
  transform: scale(0);
}
.list-leave-active {
  display: none;
}
.list-move {
  transition: all 0.5s;
}
.htooltip-inner {
  background-color: #fff;
  border: 1px solid #808d97;
  padding: 5px;
  width: 100px;
  border-radius: 5px;
  opacity: 0;
  position: absolute;
  text-decoration: none;
  visibility: hidden;
  transform: translateX(-100%);
  z-index: 10;
  transition: all 0.5s;
}
.htooltip-inner button:first-child {
  margin-right: 5px;
}
.htooltip:hover .htooltip-inner {
  cursor: pointer;
  opacity: 1;
  visibility: visible;
}
.lds-ellipsis {
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #222f3e;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
</style>



