// The Vue build version to load with the `import` command (runtime-only or
// standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";

import { store } from "./store";
import { router } from "./routes.js";

new Vue({
  el: "#app",
  store,
  router,
  components: {
    App
  },
  template: "<App/>"
});



