import Vue from "vue";
import Vuex from "vuex";
import { itemsRef } from "../firebase";

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    items: [],
    currentPage: 1,
    loading: false
  },
  getters: {
    isLoading(state) {
      return state.loading;
    },
    getTasks(state) {
      return state.items;
    },
    countTasks(state) {
      return state.items.length;
    },
    currentPage(state) {
      return state.currentPage;
    },
    getTask: state => id => {
      let index = state.items.findIndex(t => t.id == id);
      return state.items[index];
    },
    currentTasks(state) {
      return state.items.slice(
        (state.currentPage - 1) * 10,
        state.currentPage * 10
      );
    }
  },
  mutations: {
    setItems(state, items) {
      state.items = items;
      state.loading = false;
    },
    addTask(state, task) {
      state.items.unshift(task);
    },
    deleteTask(state, id) {
      let index = state.items.findIndex(t => t.id == id);
      state.items.splice(index, 1);
    },
    completeTask(state, id) {
      let index = state.items.findIndex(t => t.id == id);
      state.items[index].completed = !state.items[index].completed;
    },
    setPage(state, page) {
      state.currentPage = page;
    },
    reverseTasks(state) {
      state.items.reverse();
    },
    editTask(state, task) {
      let index = state.items.findIndex(t => t.id == task.id);
      state.items[index].title = task.title;
    }
  },
  actions: {
    addTask({ commit }, task) {
      let newItemRef = itemsRef.push({ title: task.title, completed: false,date:Date.now().toString()});
      task.id = newItemRef.key;
      commit("addTask", task);
    },
    deleteTask({ commit }, id) {
      itemsRef.child(id).remove();
      commit("deleteTask", id);
    },
    completeTask({ commit }, task) {
      console.log(task);
      itemsRef.child(`${task.id}/completed`).set(!task.completed);
      commit("completeTask", task.id);
    },
    editTask({ commit }, task) {
      itemsRef.child(`${task.id}/title`).set(task.title);
      commit("editTask", task);
    },
    async loadItems({ commit, state }) {
      state.loading = true;
      const items = [];
      await itemsRef.once("value", function(snapshot) {
        snapshot.forEach(function(data) {
          const item = {
            id: data.key,
            title: data.val().title,
            completed: data.val().completed,
            date:Date.now().toString()
          };
          items.unshift(item);
        });
      });
      commit("setItems", items);
    }
  }
});



