<template>
  <div class="container">
    <div class="row">
      <h1 class="h1">Edit Task</h1>
      <textarea
        class="task-area"
        v-model.trim="title"
        @keydown.enter="editTask"
        placeholder="Enter title for this card"
        name="task"
      ></textarea>
      <div class="task-control">
        <button class="task-add" @click="editTask">Edit Card</button>
        <button class="task-control task-change" @click="closeEdit">
          &times;
        </button>
      </div>
      <p class="box error" v-if="error">Title can't be blank</p>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return { title: "", error: false };
  },
  beforeMount() {
    this.title = this.$store.getters["getTask"](this.$route.params.id).title;
  },
  methods: {
    editTask() {
      if (this.title == "") {
        this.error = true;
        return false;
      }
      let task = {
        id: this.$route.params.id,
        title: this.title
      };
      this.$store.dispatch("editTask", task);
      this.$router.push({ path: `/page/1` });
    },
    closeEdit() {
      this.$router.push({ path: `/page/1` });
    }
  }
};
</script>
<style scoped="scoped">
.row {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.link {
  color: #78868f;
}
.link:hover {
  cursor: pointer;
  color: #222f3e;
}
</style>



