<template>
  <transition name="bounce">
    <div v-if="showAdd" key="form">
      <textarea
        class="task-area"
        placeholder="Enter title for this card"
        name="task"
        v-model.trim="title"
        @keyup.enter="addTask"
        @keydown="error = false"
      ></textarea>
      <div class="task-control">
        <button class="task-add" @click="addTask">Add Card</button>
        <button
          class="task-control task-change"
          @click="
            showAdd = !showAdd;
            error = false;
          "
        >
          &times;
        </button>
      </div>
      <p class="box error" v-if="error">Title can't be blank</p>
    </div>
    <div v-else key="openForm">
      <button class="task-add" @click="showAdd = !showAdd">Create Card</button>
    </div>
  </transition>
</template>

<script>
export default {
  data() {
    return { title: "", showAdd: true, error: false };
  },
  methods: {
    addTask() {
      if (this.title == "") {
        this.error = true;
        return false;
      }
      let task = {
        title: this.title
      };
      this.$emit("toFirstPage");
      this.error = false;
      this.title = "";
      this.$store.dispatch("addTask", task);
    }
  }
};
</script>
<style>
.error {
  background-color: #e74c3c !important;
  border: 1px solid #e74c3c !important;
  width: 100%;
  color: #fff;
  padding: 10px !important;
}
.task-area {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;

  resize: none;

  border-radius: 5px;
  border: none;
}
.task-add {
  border: none;
  color: #fff;
  font-weight: bold;
  border-radius: 5px;

  padding: 10px 15px;
  background-color: #7ca65c;
}
.task-add:hover {
  background-color: #5b7945;
  cursor: pointer;
}
.task-control {
  display: flex;
  border: none;
  color: #808d97;
  background-color: initial;
  font-size: 1rem;
}
.task-control:hover {
  color: #5b646b;
  cursor: pointer;
}
.task-change {
  align-self: center !important;
  font-size: 2rem;
  border: none;
  background-color: inherit;
}
.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}
</style>



