<template>
  <div>
    <ul class="pagination list-reset">
      <li v-for="page in pages" :key="page" class="pagination-item">
        <button
          class="pagination-button"
          :class="{ active: page == currentPage }"
          @click="pagechanged(page)"
        >
          {{ page }}
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    "per-page": {
      type: Number,
      default: 10
    },
    "total-items": {
      type: Number,
      required: true
    },
    "current-page": {
      type: Number
    }
  },
  computed: {
    pages() {
      return Math.ceil(this.totalItems / this.perPage);
    }
  },
  methods: {
    pagechanged(page) {
      this.$emit("pagechanged", page);
    }
  }
};
</script>
<style>
.pagination-item {
  display: inline-block;
  margin-right: 5px;
  margin-bottom: 5px;
}
.pagination-button {
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 10px;
  padding: 5px 10px;
}
.pagination-button:hover {
  cursor: pointer;
  border-color: #243849;
}
.pagination-item .active {
  color: #fff;
  background-color: #243849;
  border-color: #243849;
}
</style>



